<template lang="html">
<div class='projects'>
<v-container class="mt-16">
<v-row>
<v-col>
<h1 class='heading blue-grey--text text--darken-1'>Software projects</h1>
</v-col></v-row>

<v-row dense>
<v-col cols='12'
    v-for="card in cards"
    :key="card.title"
     md='4' xs='10'>

<v-card :color="card.color" dark>
  <div class="justify-space-between">
    <div>
      <v-card-title :class="card.color +'--text text--lighten-5 heading font-weight-bold' ">{{card.title}}
      </v-card-title>
  <v-card-subtitle :class="card.color +'--text text--lighten-5'">
    <v-chip class="ma-2" small :color="card.color + 'lighten-5'" label outlined>
      {{card.status}}
    </v-chip>
  </v-card-subtitle></div></div>

<v-overlay absolute :value="card.overlay">
<v-card>
<v-card-text> {{card.description}} </v-card-text>
<v-btn text  @click="card.overlay = false">
    close
</v-btn>
</v-card>
</v-overlay>

<v-card-actions>

<v-btn :color="card.color+'lighten-5'" text @click="card.overlay = !card.overlay">
Details</v-btn>
<v-spacer/>

<v-btn icon text>
  <a :href="'//'+card.src" target="_blank">  <v-icon color='grey lighten-5'>mdi-github
  </v-icon></a>
  </v-btn>
</v-card-actions>
</v-card>
</v-col>
</v-row>
</v-container>
</div>
</template>

<script>
export default {
 name:'Projects',

 data: () => ({
cards: [
        { title: 'Path Loss Prediction',
        status: 'Completed', flex: 4, color:'#004D40',overlay: false,
        description:'Path loss prediction is of great significance for the performance optimization of wireless networks. With the development and deployment of the fifth-generation (5G) mobile communication systems, new path loss prediction methods with high accuracy and low complexity for such different models are proposed and after all experiment we introduced classical machine learning model to predict the loss path.',
        src:'github.com/sg0055/Path-Loss-Prediction-Based-on-Machine-Learning'
      },
        { title: 'Blockchain Based E-Voting',
        status: 'Completed', flex: 4, color:'pink',overlay: false,
        description:'Current Voting system involves many layers of authorities which make it complex time-consuming and costly.In this project we conduct voting on Blockchain environment developed on Python which make it less time consuming, tamper-proof and easy with real time counting.',
        src:'github.com/sg0055/Blockchain-voting'
      },
      { title: 'PHP based Discussion Forum',
      status: 'On Update', flex: 4, color:'#880E4F',overlay: false,
      description:'Integrated Discusssion forum based on PHP 7 can be deploy on any site with PHP support. It has login portals and all basic requirements of a discussion forum like creating topic, creating subject with privilege restriction and all data stored in SQL Database.',
      src:'github.com/sg0055/PHP_discussuon_Forum'
    },
    { title: 'Hygiene and Pollution Monitoring',
              status: 'Completed', flex: 4, color:'#263238',overlay: false,
              description:"Monitoring air for gases like Co2 and Ammonia in environment. It uses MQ135 gas sensor and DHT11  sensor to measure gas levels and temperature respectively using Arduino and GSM module to send real time updates",
              src:'github.com/sg0055/IOT-Based-Hygiene-and-Pollution-Monitoring-System'
  },

  { title: 'Real-Time Vehicle Counting System',
            status: 'Completed', flex: 4, color:'#B71C1C',overlay: false,
            description:"Moving vehicle detection, tracking, and counting are very critical for traffic flow monitoring, planning, and controlling. Implemented in Visual python code with OpenCV development kits.",
            src:'github.com/sg0055/-REAL-TIME-MOVING-VEHICLE-COUNTING-SYSTEM'
},

{ title: 'Emotion Recognition (PCA algo)',
          status: 'Completed', flex: 4, color:'#1A237E',overlay: false,
          description:"Humans are used to taking in non verbal cues from facial emotions. Now computers are also getting better to reading emotions. The emotions can be classified into 7 classes happy, sad, fear, disgust, angry, neutral and surprise using PCA algorithm to classify them in MATLAB.",
          src:'github.com/sg0055/-REAL-TIME-MOVING-VEHICLE-COUNTING-SYSTEM'
},
      ],
    }),

}
</script>

<style lang="css" scoped >

</style>
